body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    margin-top: -18px;
    padding: 0; 
    width: inherit;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    font-size: 12px
}

.navbar li {
    display: block;
    margin: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    padding: 0 3px;
    text-align: left
}
.warning-message {
    color: rgb(155, 36, 0);
    background-color: rgb(253, 238, 236);
    padding: 0 12px
  }
.search {
    padding-top: 6px;
    padding-bottom: 12px
}

input {
    margin-right: 12px;
    text-align: center;
    border: 2px solid rgba(196, 226, 196, 1); /* light green */
    height: 20px;
    font-size: 16px;
    font-family: "Monaco", "Consolas", monospace;
    padding: 6px 6px;
}

.search-link {
    padding: 6px 9px;
    height: 20px;
    border: 2px solid hsl(208, 56%, 82%) /* Same colour as Footer */
}
.App {
  text-align: center;
  font-family: "Alegreya", serif;
  margin: 0 0 0 0;
  width: 100%;
  height: 100vh;
  padding: 0;
  /* background-color: rgb(254, 252, 250) */
  background: rgb(238, 243, 238);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(196,226,196,0.05)), to(rgba(183,211,235,0.05)));
  background: -webkit-linear-gradient(top, rgba(196,226,196,0.05) 0%, rgba(183,211,235,0.05) 100%);
  background: linear-gradient(180deg, rgba(196,226,196,0.05) 0%, rgba(183,211,235,0.05) 100%);
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr auto;
  grid-template-areas: "body" "footer";
}

p {
  padding-left: 6px;
  padding-right: 6px;
  margin: 9px 0
}

.word,.home,.anagrams,.anagrams-home,.countdown,.countdown-home {
  grid-area: body;
  overflow-y: auto
}

h1 {
  width: inherit;
  padding: 9px 6px;
  margin: 0 0 6px;
  background-color: rgb(196, 226, 196); /* pale green */
  color: rgb(58, 114, 58) /* a darker, brighter green */
}

.title {
  color: black
}

h2 {
  margin-top: 12px;
  margin-bottom: 3px
}

.divider {
  height: 2px;
  width: 50%;
  margin-left: 25%;
  margin-right: 25%;
  border-bottom: 2px solid rgb(196, 226, 196)
}

.dictionaries {
  background-color: rgb(215, 247, 247);
  padding: 12px 6px;
  margin-top: 12px;
  margin-bottom: 0
}
.footer {
    grid-area: footer;
    padding: 6px;
    /* This is the color of links, but lighter */
    background-color: hsl(208, 56%, 82%);
}

a:link {
    font-weight: bold;
    color: #337ab7;
    text-decoration: none
}

a:visited {
    font-weight: bold;
    color: #337ab7;
    text-decoration: none
}

a:hover {
    color: #23527c;
    text-decoration: underline
}  
